$(".work-card").mouseover(function () {
    $(".work-card").removeClass("_active")
    $(this).addClass("_active")
})

$(".product-card").click(function () {
    $(this).toggleClass("_open")
    $(this).find(".product-card__content").slideToggle()
})

$(".benefit-card").click(function () {
    if($(this).hasClass("_active")){
        $(".benefit-card").removeClass("_active")
        $(".benefit-card__hidden").slideUp()
    }else{
        $(".benefit-card").removeClass("_active")
        $(".benefit-card__hidden").slideUp()
        $(this).addClass("_active")
        $(this).find(".benefit-card__hidden").slideDown()
    }
})
